			indicatorDots: false,
			duration: 500,
			shopID: 0,
			shopTYPE: 0,
			shopname: 0,
			imgs: [
				'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=4067865560,4104866691&fm=15&gp=0.jpg'
			],
			itemList: [{
