						setTimeout(() => {
							uni.showModal({
								title: "取号成功是否点餐",
								content: "是否去点餐",
								success: res => {
									console.log(res)
									if (res.confirm == true) {
										console.log(1)
										that.isShow = true
									}
								}
							})
						}, 2000)

					})
				})

			} else {
				uni.showToast({
					title: '未登录~',
					duration: 3000
				})
				uni.navigateTo({
					url: '/pages/sigUp/sigUp'
				})
			}
		},
		swiperChange(e) {
			this.current = e.detail.current + 1;
		},
		Todianchan(e) {
			if (!uni.getStorageSync('phone')) {
				uni.showToast({
					title: '未登录~',
					duration: 3000
				})
				uni.navigateTo({
					url: '/pages/sigUp/sigUp'
				})
			} else {
				uni.navigateTo({
					url: '/pages/paidui/paidui?aid=' + this.shopID + "&name=" + this.shopname + "&did=" + this.did
				})
			}
		},
		passwordTl: function(tl) {
			var reg = /^(\d{3})\d{4}(\d{4})$/;
			return (tl.replace(reg, "$1****$2"))
		}
	}
}
